.header {
  margin-top: 60px;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.header-title1 {
  position: absolute;
  
  font-size: 20px;
}


.headerImg{
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
    border-radius: 30px;
}
.post {
  width: 385px;
  margin: 0px 25px 40px 25px;
}

.postImg {
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-radius: 25px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Varela Round", sans-serif;
  font-size: 11px;
  color: #be9656;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
  cursor: pointer;
}

.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  color: #999;
  margin-top: 15px;
}

.postDesc {
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  color: black;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.posts{
    flex: 9 1;
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
    
    
    
}

    
  
  
.sidebar {
  flex: 3 1;
  margin: 20px;
  padding-bottom: 30px;
  background-color: darkblue;
  color: white;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.sidebarItem {
  display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebarTitle2 {
  width: 100%;
    padding: 0px;
    margin: 10px;
    border-top: 5px solid gold;
    border-bottom: 5px solid gold;
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    line-height: 20px;
    text-align: center;
}

.sidebarTitle1{
  width: 100%;
  padding: 0px;
  margin: 10px;
  border-top: 5px solid gold;
  border-bottom: 5px solid gold;
  font-size: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  line-height: 20px;
  text-align: center;
}

.sidebarItem > img {
  margin-top: 15px;
  height: 350px;
  width: 300px;
  cursor: pointer;
  border-radius: 20px;
  
}

.sidebarItem > p {
  padding: 30px;
}

.sidebarList {
  list-style: none;
  margin-bottom: 30px;
}

.sidebarListItem {
  display: inline-block;
      width : 60%;
      margin-top: 10px;
      cursor: pointer;
}

.sidebarSocial {
  margin-top: 15px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebarIcon{
  font-size: 30px;
  margin-left: 18px;
  color: gold
}
.home{
    display: flex;
}
.top {
  width: 100%;
  z-index: 999;
  width: 100%;
    height: 100px;
    background-color: darkblue;
    color: whitesmoke;
    position:-webkit-sticky;
    position:sticky;
    top: 1px;
    display:flex;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 15px;
    
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    
}

.topLeft {
  flex: 3 1;
    background-color: transparent;
    color: white;
    margin-left: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.topRight {
  flex: 3 1;
  background-color: transparent;
  color: gold;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topCenter {
  flex : 8 1;
  background-color: transparent;
  color: gold;
  font-size: 30px;
    
}

.topIcon {
  color:gold;
    cursor: pointer;
    font-size: 30px;
    margin-right: 15px;
}

.topImg {
  width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
    cursor: pointer;
}

.topList {
  display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.topListItem {
  margin-right: 20px;
  font-weight: 700px;
  cursor: pointer;
}

.topSearchIcon {
  font-size: 30px ;
  cursor: pointer;
  margin-left: 15px;
}


.single{
    display: flex;
}
.singlePost {
  flex: 9 1;
}

.singlePostWrapper {
  padding: 20px;
  padding-right: 0;
  display: flex;
  flex-direction: column;
  
  
}

.singlePostImg {
  width: 100%;
  height: 300px;
  border-radius: 15px;
  object-fit: cover;
}

.singlePostTitle {
  text-align: center;
  margin: 10px;
  font-family: "Lora", serif;
  font-size: 28px;
}

.singlePostTitleInput {
  margin: 10px;
  font-family: "Lora", serif;
  font-size: 28px;
  text-align: center;
  border: none;
  color: black;
  border-bottom: 1px solid lightgray;
}

.singlePostTitleInput:focus {
  outline: none;
}

.singlePostEdit {
  float: right;
  font-size: 16px;
}

.singlePostIcon {
  margin-left: 10px;
  cursor: pointer;
}

.singlePostIcon:first-child {
  color: darkblue;
}

.singlePostIcon:last-child {
  color: red;
}

.singlePostInfo {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-family: "Varela Round", sans-serif;
  color: #b39656;
}

.singlePostDesc {
  color: black;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDesc::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

.singlePostDescInput {
  border: none;
  color: #666;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDescInput:focus {
  outline: none;
}
.singlePostButton {
  width: 100px;
  border: none;
  background-color: darkblue;
  padding: 5px;
  color: white;
  border-radius: 20px;
  cursor: pointer;
  align-self: flex-end;
  margin-top: 20px;
  font-weight: bolder;
}

.write {
  padding-top: 50px;
}

.writeImg {
  margin-left: 150px;
  width: 70vw;
  height: 250px;
  border-radius: 10px;
  object-fit: cover;
}

.writeForm {
  position: relative;
  
}

.writeFormGroup {
  margin-left: 150px;
  display: flex;
  align-items: center;
  margin-right: 50px;
  
}

.writeIcon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: rgb(121, 118, 118);
  cursor: pointer;
}

.writeInput {
  font-size: 30px;
  border: none;
  padding: 20px;
  width: 70vw;
  margin-bottom: 30px;
  border-radius: 20px;
  border: none;
  border: 1px solid darkgreen;
  background-color: aqua;
}

.writeInput:focus {
  outline: none;
}

.writeText {
  font-size: 20px;
  height: 100vh;
}

.writeSubmit {
  position: absolute;
  top: 20px;
  right: 50px;
  color: white;
  background-color: darkblue;
  padding: 10px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bolder;
}

.settings {
  display: flex;
}

.settingsWrapper {
  flex: 9 1;
  padding: 20px;
}

.settingsTitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.settingsUpdateTitle {
  font-size: 30px;
  margin-bottom: 25px;
  color: darkblue;
  font-weight: bolder;
}

.settingsDeleteTitle {
  color: red;
  font-size: 25px;
  cursor: pointer;
  font-weight: bolder;
  
}

.settingsForm {
  display: flex;
  flex-direction: column;
}

.settingsPP {
  display: flex;
  align-items: center;
  margin: 10px 0;
  
}

.settingsPP > img {
  width: 70px;
  height: 70px;
  border-radius: 20px;
  object-fit: cover;
}

.settingsPPIcon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: blue;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  cursor: pointer;
}

.settingsForm > label {
  font-size: 20px;
  margin-top: 20px;
  font-weight: bolder;
}

.settingsForm > input {
  color: gray;
  margin: 10px 0;
  height: 30px;
  border: none;
  border-bottom: 1px solid lightgray;
  border-radius: 20px;
}

.settingsSubmit{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 15px;
    color: white;
    background-color: darkblue;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
    font-weight: bolder;
}
.login {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loginTitle {
  font-size: 50px;
}

.loginForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.loginForm > label {
  margin: 10px 0;
  font-weight: bolder;
}

.loginInput {
  padding: 10px;
  background-color: inherit;
  padding: 10px;
  border: none;
  border-bottom: 1px solid darkgreen;
  border-radius: 20px;
  font-size: 18px;

}

.loginButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: darkblue;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px;
  font-weight: bolder;
}

.loginButton:disabled{
  cursor: not-allowed;
  background-color: rgb(252, 173, 173);
}

.loginRegisterButton {
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: teal;
  cursor: pointer;
  border: none;
  padding: 10px;
  color: white;
  border-radius: 10px;
}

.register {
  height: calc(100vh - 50px);
