  align-items: center;
  justify-content: center;
}

.registerTitle {
  font-size: 50px;
}

.registerForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.registerForm > label {
  margin: 10px 0;
  font-weight: bolder;
}

.registerInput {
  padding: 10px;
  background-color: inherit;
  padding: 10px;
  border: none;
  border-bottom: 1px solid darkgreen;
  border-radius: 20px;
  font-size: 18px;
}

.registerButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: darkblue;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 10px;
  font-weight: bolder;
}

.registerLoginButton {
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: lightcoral;
  cursor: pointer;
  border: none;
  padding: 10px;
  color: white;
  border-radius: 10px;
}

